// Scroll To Top
window.addEventListener("scroll", displayTop);

function displayTop() {
  const up = document.querySelector("#up");

  if (up) {
    if (
      document.body.scrollTop >= 375 ||
      document.documentElement.scrollTop >= 375
    ) {
      up.setAttribute("data-open", "true");
    } else {
      up.setAttribute("data-open", "false");
    }

    up.addEventListener("click", getTop, scrollToTop);
  }
}

// IE and Edge Function
function getTop() {
  document.body.scrollTop = 0;
  document.documentElement.scrollTop = 0;
}

// Angular Compatible Function
function scrollToTop() {
  let scrollStep = -window.scrollY / (500 / 15);
  let scrollInterval = setInterval(() => {
    if (window.scrollY !== 0) {
      window.scrollBy(0, scrollStep);
    } else clearInterval(scrollInterval);
  }, 15);
}
