if (entryValue) {
  entryValue.addEventListener("keyup", () => {
    let value = entryValue.value;
    if (value !== "") {
      sections = value.split("\n");

      clearCanvas();
      totalSections = sections.length;
      arc = canvasConstantTAU / totalSections;
      sections.forEach(drawSections);
    }

    if (value.length <= 1) {
      value = "";
      sections = value;

      clearCanvas();
      emptySections();
    }

    // console.log("sections", sections, sections.length);
  });

  entryValue.addEventListener("keydown", () => {
    let value = entryValue.value;
  });
}

