// canvas
// Generate random float in range min-max:
const random = (m, M) => Math.random() * (M - m) + m;

// const totalSections = sections.length;
let totalSections = sections.length;
const spinElement = document.querySelector("#spin");
const canvasElement = document.querySelector("#wheel");
const canvasContext = canvasElement.getContext("2d");
canvasContext.imageSmoothingEnabled = true;

const result = document.querySelector("#result");
const article = document.querySelector("#article");
const reset = document.querySelector("#reset");
const canvasDiameter = canvasContext.canvas.width;
const canvasRadius = canvasDiameter / 2;
const canvasCircumference = Math.PI;
const canvasConstantTAU = 2 * canvasCircumference;
// const arc = canvasConstantTAU / totalSections;
let arc = canvasConstantTAU / totalSections;
const friction = 0.991; // 0.995=soft, 0.99=mid, 0.98=hard
const angleVelocityMin = 0.002; // Below that number will be treated as a stop
let angleVelocityMax = 0; // Random ang.vel. to accelerate to
let angleVelocity = 0; // Current angular velocity
let angle = 0; // Angle rotation in radians
let isSpinning = false;
let isAccelerating = false;
let animateFrame = null; // Engine's requestAnimationFrame

//* Get index of current sector */
const getIndex = () =>
  Math.floor(totalSections - (angle / canvasConstantTAU) * totalSections) %
  totalSections;

function emptySections() {
  canvasContext.beginPath();
  canvasContext.arc(
    canvasRadius,
    canvasRadius,
    canvasRadius - 5,
    0,
    2 * Math.PI
  );
  canvasContext.lineWidth = 5;
  canvasContext.strokeStyle = "#333";
  canvasContext.stroke();
}

//* Draw sectors and prizes texts to canvas */
function drawSections(section, index) {
  const angle = arc * index;
  canvasContext.save();
  // COLOR
  canvasContext.beginPath();
  canvasContext.fillStyle = colors[index % 12];
  canvasContext.moveTo(canvasRadius, canvasRadius);
  canvasContext.arc(
    canvasRadius,
    canvasRadius,
    canvasRadius - 5, // Add negative space around the wheel
    angle,
    angle + arc
  );
  canvasContext.lineTo(canvasRadius, canvasRadius);
  canvasContext.fill();

  // OUTLINE
  canvasContext.lineWidth = 5;
  canvasContext.strokeStyle = "#333";
  canvasContext.stroke();

  // TEXT
  canvasContext.translate(canvasRadius, canvasRadius);
  canvasContext.rotate(angle + arc / 2);
  canvasContext.textAlign = "right";
  canvasContext.fillStyle = "#333";
  canvasContext.font = "bold 20px sans-serif";
  canvasContext.fillText(section, canvasRadius - 20, 5);
  //
  canvasContext.restore();
}

//* CSS rotate CANVAS Element */
function rotate() {
  const section = sections[getIndex()];
  canvasContext.canvas.style.transform = `rotate(${
    angle - canvasCircumference / 2
  }rad)`;

  if (!angleVelocity) {
    result.textContent = section;

    article.textContent = section;
    article.parentNode.removeAttribute("disabled");

    reset.removeAttribute("disabled");
  }
}

function frame() {
  if (!isSpinning) return;

  if (angleVelocity >= angleVelocityMax) isAccelerating = false;

  // Accelerate
  if (isAccelerating) {
    angleVelocity ||= angleVelocityMin; // Initial velocity kick
    angleVelocity *= 1.06; // Accelerate
    audio.playbackRate = 1.06;
  }

  // Decelerate
  else {
    isAccelerating = false;
    angleVelocity *= friction; // Decelerate by friction
    audio.playbackRate = friction;

    // SPIN END:
    if (angleVelocity < angleVelocityMin) {
      isSpinning = false;
      angleVelocity = 0;
      cancelAnimationFrame(animateFrame);
      if (isOn === true) {
        stopSound();
      }
    }
  }

  angle += angleVelocity; // Update angle
  angle %= canvasConstantTAU; // Normalize angle
  rotate(); // CSS rotate!
}

function engine() {
  frame();
  animateFrame = requestAnimationFrame(engine);
}

spinElement.addEventListener("click", () => {
  if (isSpinning) return;
  isSpinning = true;
  isAccelerating = true;
  angleVelocityMax = random(0.25, 0.4);
  engine(); // Start engine!
  if (isOn === true) {
    playSound();
  }
});

// INIT!
sections = initialSections;
formatInputSectionValues(sections);

sections.forEach(drawSections);
rotate(); // Initial rotation

result.textContent = "";
let initialResult = result.textContent;

article.textContent = "selected item";
let initialArticle = article.textContent;

article.parentNode.setAttribute("disabled", "");
reset.setAttribute("disabled", "");

// Prep
function formatInputSectionValues(items) {
  entryValue.value = items;
  let formattedValues = entryValue.value.replace(/,/g, "\n");
  entryValue.value = formattedValues;
}

function repaintSections() {
  totalSections = sections.length;
  arc = canvasConstantTAU / totalSections;
  sections.forEach(drawSections);
}

// Remove
const remove = document.querySelector("#remove");

remove.addEventListener("click", removeSection);

function removeSection() {
  const item = sections[getIndex()];
  sections = sections.filter((section) => section !== item);
  // console.log("winner removed =>", `${item} from ${sections}`);

  formatInputSectionValues(sections);
  clearCanvas();
  repaintSections();

  if (sections.length <= 0) {
    emptySections();
  }

  if (article.parentNode.hasAttribute("disabled") !== "false") {
    article.parentNode.setAttribute("disabled", "");
    article.textContent = initialArticle;
    result.textContent = initialResult;
  }
}

// Clear and Reset
reset.addEventListener("click", resetCanvas);

function clearCanvas() {
  canvasContext.clearRect(
    0,
    0,
    canvasContext.canvas.width,
    canvasContext.canvas.height
  );
  canvasContext.arc(
    canvasRadius,
    canvasRadius,
    canvasRadius - 5,
    0,
    2 * Math.PI
  );
  canvasContext.fillStyle = "rgba(32, 35, 42, .15)";
  canvasContext.fill();
}

function resetCanvas() {
  sections = initialSections;
  formatInputSectionValues(sections);
  clearCanvas();
  repaintSections();

  canvasContext.canvas.style.transform = `rotate(-1.5708rad)`;

  result.textContent = initialResult;
  article.textContent = initialArticle;

  if (reset.hasAttribute("disabled") !== "false") {
    reset.setAttribute("disabled", "");
  }
}

const audio = new Audio("./assets/sounds/flow.mp3");
const audioOn = document.querySelector("#sound");
let isOn = false;

function getSound() {
  if (audioOn) {
    audioOn.addEventListener("change", () => {
      if (audioOn.checked) {
        isOn = true;
      } else {
        isOn = false;
      }
    });
  }
}

function playSound() {
  audio.currentTime = 0;
  audio.play();
  audio.loop = true;
  audio.volume = 0.3;
}

function stopSound() {
  audio.pause();
}
